<template>
  <v-container fluid>
    <v-row dense>
      <p></p>
      <v-col v-if="cards !=null" cols="12">
        <v-text-field
          v-model="searchTerm"
          label="Rechercher un lien"
          solo
          :clearable="true"
          dense
          hide-details
          prepend-inner-icon="mdi-magnify"
          @click:clear="clearSearch"
        ></v-text-field>
      </v-col>
      <v-col v-for="card in filteredCards" :key="card.title" :cols="card.flex">
        <cda-card v-bind:card="card"></cda-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
// @ is an alias to /src
import CdaCard from "@/components/CdaCard.vue";
import Vue from 'vue'
// import dataCards from "/data-cards2.json"
export default {
  name: "Liens",
  components: {
    CdaCard,
  },
  data: () => ({
    searchTerm: '',
    cards: null,
  }),
  methods: {
    clearSearch() {      
      this.searchTerm = ''
    }
  },
  beforeMount: async function () {
    // let response = await Vue.http.get('/data-cards2.json');
    let response = await Vue.http.get(process.env.BASE_URL+`/data-cards2.json`);
    this.cards = response.data.data
    // console.log(process.env)
  },
  computed: {
    filteredCards() {
      if (!this.cards || !this.cards.length > 0) {
        return []
      }
      let filteredCards = this.cards.filter((card) => {
        if (!this.searchTerm) return true
        let found = card.title.toLowerCase().includes(this.searchTerm.toLowerCase())        
        if (found) {
          return true
        }
        if (card.tags && card.tags.length > 0) {
          for (let index = 0; index < card.tags.length; index++) {
            const tag = card.tags[index];
            if ( tag.toLowerCase().includes(this.searchTerm.toLowerCase())){
              return true
            }       
          }          
        }
        return false 
      })
      return filteredCards
    },
  },
};
</script>